import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { EmailService } from '../../angular-services/email.service';

@Component({
  selector: 'app-ouvidoria',
  templateUrl: './ouvidoria.component.html',
  styleUrls: ['./ouvidoria.component.css']
})
export class OuvidoriaComponent implements OnInit {

  loading: boolean = false;
  formContact: FormGroup;

  resposta: string;
  tipoResposta: string;

  constructor(private emailService: EmailService) { }

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.formContact = new FormGroup({
      name: new FormControl('', Validators.required),
      email: new FormControl('', Validators.required),
      phone: new FormControl('', Validators.required),
      message: new FormControl('', Validators.required)
    });
  }

  send() {
    if (this.formContact.valid) {
      let data = this.formContact.value;
      let date = new Date();
      let body = `
        Nome: ${data.name}
        Email: ${data.email}
        Celular: ${data.phone}
        Mensagem: ${data.message}
        Data: ${date}
      `;
      this.emailService.sendEmail(body, 'Ouvidoria').subscribe(
        response => {
          if(response) {
            this.resposta = response.message;
            if (response.status == 'success') {
              this.tipoResposta = response.status;
            } else {
              this.tipoResposta == 'danger';
            }
          }else{
            this.tipoResposta = 'danger';
            this.resposta = 'Falha ao enviar o email. Tente novamente em instantes.';
          }
        }
      );
    }
  }

}
